<script lang="ts">import { createEventDispatcher } from 'svelte';
export let placeholder = '';
const dispatch = createEventDispatcher();
let input = '';
const onClick = () => {
    if (input === '' || input === ' ') {
        return;
    }
    dispatch('add', input);
    input = '';
};
</script>

<div class="Input">
  <input class="Input__input" type="text" bind:value={input} {placeholder} />
  <button on:click={onClick}>Add</button>
</div>

<style lang="scss">.Input {
  display: flex;
}
.Input__input {
  margin-right: 1rem;
}</style>
